<template>
    <v-app id="register">
        <v-content>
            <v-container
                fluid
                fill-height
            >
                <v-layout
                    align-center
                    justify-center
                >
                    <v-flex
                        xs12
                        sm8
                        md4
                    >
                        <v-form
                            ref="form"
                            v-model="valid"
                            lazy-validation
                        >
                            <v-card class="elevation-12">
                                <v-toolbar
                                    color="primary"
                                    dark
                                    flat
                                >
                                    <v-toolbar-title></v-toolbar-title>
                                    <v-spacer></v-spacer>
                                </v-toolbar>
                                <v-card-text>
                                    <v-text-field
                                        label=""
                                        name="name"
                                        prepend-icon="person"
                                        type="email"
                                        v-model.trim="name"
                                        :counter="10"
                                        :rules="nameRules"
                                        required
                                    ></v-text-field>

                                    <v-text-field
                                        label=""
                                        name="email"
                                        prepend-icon="person"
                                        type="email"
                                        :counter="50"
                                        :rules="emailRules"
                                        v-model.trim="email"
                                        required
                                    ></v-text-field>

                                    <v-text-field
                                        label=""
                                        name="password"
                                        prepend-icon="lock"
                                        type="password"
                                        :counter="20"
                                        :rules="passwordRules"
                                        v-model="password"
                                        required
                                    ></v-text-field>

                                    <v-text-field
                                        label=" "
                                        name="password_confirm"
                                        prepend-icon="lock"
                                        type="password"
                                        :counter="20"
                                        :rules="password_confirmRules"
                                        v-model="password_confirm"
                                        required
                                    ></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn
                                        to="/"
                                    >
                                        
                                    </v-btn>
                                    <v-btn color="primary" @click="validate"></v-btn>
                                </v-card-actions>
                            </v-card>
                        </v-form>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>
    import md5 from 'md5';

    export default {
        name: 'register',
        data: () => ({
            valid: true,
            name: '',
            nameRules: [
                v => !!v || '  .',
                v => (v && v.length <= 10) || ' 10 .',
            ],
            email: '',
            emailRules: [
                v => !!v || '  .',
                v => /.+@.+\..+/.test(v) || '   .',
            ],
            password: '',
            passwordRules: [
                v => !!v || '  .',
                v => (v && v.length >= 6) || ' 6 .',
            ],
            password_confirm: '',
            password_confirmRules: [
                v => !!v || '  .',
                v => (v && v.length >= 6) || ' 6 .',
            ],

            usersRef: null
        }),
        methods: {
            validate () {
                if (this.$refs.form.validate()) {
                    if (this.password !== this.password_confirm) {
                        this.$alert.showAlertToWarning(' ', '  ');
                        return;
                    }
                    this.doRegister();
                }
            },
            doRegister() {
                const loader = this.$common.getLoader(this);
                this.$firebase.auth()
                    .createUserWithEmailAndPassword(this.email, this.password)
                    .then(({user}) => {
                        user.updateProfile({
                            displayName: this.name,
                            photoURL: `http://www.gravatar.com/avatar/${md5(user.email)}?d=identicon`
                        }).then(async () => {
                            await this.saveUserToUsersRef(user).then(() => {
                                this.$store.dispatch('setUser', user);
                                this.$snack['success']({
                                    text: ' .  .',
                                });
                                this.$router.push('/login');
                            });
                        }).catch(err => {
                            this.$alert.showAlertToWarning(err.message);
                        }).finally(() => {
                            loader.hide();
                        });
                    }).catch(err => {
                    this.$alert.showAlertToWarning(err.message);
                    loader.hide();
                });
            },

            saveUserToUsersRef(user) {
                return this.usersRef.child(user.uid).set({
                    name: user.displayName,
                    avatar: user.photoURL
                });
            }
        },
        created() {
            this.usersRef = this.$firebase.database().ref('users');
        }
    };
</script>
