import Vue from 'vue';
import Swal from 'sweetalert2';

const alert = {
    install (Vue) {
        /**
         *   
         */
        const alertMethods = {
            toHtml (text) {
                return (text && text.length > 0) ? text.replace('\n', '<br>') : '';
            },
            /**
             *   alert
             * @param title 
             */
            showCommonAlert (title, positiveCallback) {
                this.showAlertToQuestion('warning', '', title, '', positiveCallback);
            },

            /**
             *     alert
             * @param positiveCallback   ()
             */
            showAlertToSaveWithMessage (message, positiveCallback) {
                this.showAlertToQuestion('warning', // 
                    "",
                    message,                //  
                    '', //  
                    positiveCallback, //   
                    ''); //  
            },

            /**
             *   alert
             * @param positiveCallback   ()
             */
            showAlertToSave (positiveCallback) {
                this.showAlertToQuestion('question', // 
                    '?', //   
                    null,
                    '', //  
                    positiveCallback, //   
                    ''); //  
            },
            /**
             *   alert
             * @param positiveCallback   ()
             */
            showAlertToUpdate (positiveCallback) {
                this.showAlertToQuestion('question', // 
                    '?', //   
                    null,
                    '', //  
                    positiveCallback, //   
                    ''); //  
            },
            /**
             *   alert
             * @param positiveCallback   ()
             */
            showAlertToDelete (positiveCallback) {
                this.showAlertToQuestion('error', // 
                    '?', //   
                    null,
                    '', //  
                    positiveCallback, //   
                    ''); //  
            },
            /**
             *  alert (, )
             * @param title 
             * @param message 
             * @param positiveCallback   ()
             * @param negativeCallback   ()
             */
            showAlertToRetry (title, message, positiveCallback, negativeCallback) {
                this.showAlertToQuestion('error',
                    title,
                    message,
                    '',
                    positiveCallback,
                    '',
                    negativeCallback);
            },

            /**
             *  alert
             * @param title 
             * @param message 
             * @param positiveTxt   
             * @param positiveCallback    ()
             * @param negativeTxt   
             * @param negativeCallback    ()
             */
            showAlertToQuestion (type, title, message, positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                Swal.fire({
                    type: type,
                    title: title,
                    html: this.toHtml(message),
                    allowOutsideClick: false,
                    showCancelButton: !!negativeTxt,
                    confirmButtonText: positiveTxt,
                    cancelButtonText: negativeTxt
                }).then(result => {
                    if (result.value) {
                        if (positiveCallback) {
                            positiveCallback();
                        }
                    } else {
                        if (negativeCallback) {
                            negativeCallback();
                        }
                    }
                });
            },

            /**
             *  alert
             * @param message 
             * */
            showAlertToInfo (message) {
                Swal.fire({
                    type: 'warning',
                    title: 'Information!',
                    html: this.toHtml(message),
                    allowOutsideClick: false,
                    confirmButtonText: ''
                });
            },
            showAlertToWarning (title, message) {
                Swal.fire({
                    type: 'warning',
                    title: title,
                    html: this.toHtml(message),
                    allowOutsideClick: false,
                    confirmButtonText: ''
                });
            },
            /**
             * Vue     alert
             * @param title 
             * @param message 
             * @param positiveCallback  
             */
            showAlertToExit (title, message, positiveCallback) {
                Swal.fire({
                    type: 'info',
                    title: title,
                    html: this.toHtml(message),
                    allowOutsideClick: false,
                    confirmButtonText: ''
                }).then(() => {
                    //  positive callback 
                    if (positiveCallback) {
                        positiveCallback();
                    }
                });
            },
            /**
             *    alert
             * @param positiveTxt
             * @param positiveCallback
             * @param negativeTxt
             * @param negativeCallback
             */
            showModalSuccessToProcess(positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                this.showModalSuccess('.', positiveTxt, positiveCallback, negativeTxt, negativeCallback);
            },
            /**
             *    alert
             * @param positiveTxt    // 
             * @param positiveCallback   () // 
             * @param negativeTxt    // 
             * @param negativeCallback   () // 
             */
            showModalSuccessToSave (positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                this.showModalSuccess('.', positiveTxt, positiveCallback, negativeTxt, negativeCallback);
            },
            /**
             *    alert
             * @param positiveTxt    // 
             * @param positiveCallback   () // 
             * @param negativeTxt    // 
             * @param negativeCallback   () // 
             */
            showModalSuccessToUpdate (positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                this.showModalSuccess('.', positiveTxt, positiveCallback, negativeTxt, negativeCallback);
            },
            /**
             *    alert
             * @param positiveTxt    // 
             * @param positiveCallback   () // 
             * @param negativeTxt    // 
             * @param negativeCallback   () // 
             */
            showModalSuccessToDelete (positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                this.showModalSuccess('.', positiveTxt, positiveCallback, negativeTxt, negativeCallback);
            },
            /**
             *   alert
             * @param title 
             * @param positiveTxt    // 
             * @param positiveCallback   () // 
             * @param negativeTxt    // 
             * @param negativeCallback   () // 
             */
            showModalSuccess (title, positiveTxt, positiveCallback, negativeTxt, negativeCallback) {
                Swal.fire({
                    type: 'success',
                    title: title,
                    allowOutsideClick: true,
                    showCancelButton: !!negativeTxt,
                    confirmButtonText: positiveTxt,
                    cancelButtonText: negativeTxt
                }).then(result => {
                    if (result.value) {
                        if (positiveCallback) {
                            positiveCallback();
                        }
                    } else {
                        if (negativeTxt) {
                            if (negativeCallback) {
                                negativeCallback();
                            }
                        } else {
                            if (positiveCallback) {
                                positiveCallback();
                            }
                        }
                    }
                });
            }
        };

        Vue.prototype.$alert = alertMethods;
    }
};

Vue.use(alert);
