import Vue from 'vue';
import _get from 'lodash/get';
import {RotateSquare2} from 'vue-loading-spinner';

let timer;
const common = {};

common.install = function (Vue) {
    /**
     *   
     */
    const common = {

        // resize callback
        debounce(func, time = 300) {
            (function (event) {
                if (timer) clearTimeout(timer);
                timer = setTimeout(func, time, event);
            })();
        },

        //   
        getLoader(self) {
            return self.$loading.show({
                container: self.$el,
                canCancel: false,
                backgroundColor: '#111',
            }, {
                default: self.$createElement(RotateSquare2)
            });
        },

        // obj  id    
        getIdMapper(obj) {
            return {
                id: _get(obj, 'id', null)
            };
        },

        /**
         * ,  path   _get   , numeraljs   format 
         * @param data
         * @param propertyPath
         * @returns {*}
         */
        formatNumeral(data, propertyPath) {
            return Vue.prototype.$n(_get(data, propertyPath, null)).format();
        },

    };

    Vue.prototype.$common = common;
};

Vue.use(common);
