<template>
    <v-app id="login">
        <v-content>
            <v-container
                fluid
                fill-height
            >
                <v-layout
                    align-center
                    justify-center
                >
                    <v-flex
                        xs12
                        sm8
                        md4
                    >
                        <v-form
                            ref="form"
                            v-model="valid"
                            lazy-validation
                        >
                            <v-card class="elevation-12">
                                <v-toolbar
                                    color="primary"
                                    dark
                                    flat
                                >
                                    <v-toolbar-title></v-toolbar-title>
                                    <v-spacer></v-spacer>
                                </v-toolbar>
                                <v-card-text>
                                    <v-text-field
                                        label=""
                                        name="email"
                                        prepend-icon="person"
                                        type="email"
                                        :counter="50"
                                        :rules="emailRules"
                                        v-model.trim="email"
                                        required
                                    ></v-text-field>

                                    <v-text-field
                                        label=""
                                        name="password"
                                        prepend-icon="lock"
                                        type="password"
                                        :counter="20"
                                        :rules="passwordRules"
                                        v-model="password"
                                        @keyup.enter="validate"
                                        required
                                    ></v-text-field>
                                </v-card-text>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="primary" @click="validate"></v-btn>
                                </v-card-actions>
                            </v-card>
                            <router-link class="d-block text-center mt-5" to="/register">
                                 ?
                            </router-link>
                        </v-form>
                    </v-flex>
                </v-layout>
            </v-container>
        </v-content>
    </v-app>
</template>

<script>
    export default {
        name: 'login',
        data: () => ({
            valid: true,
            email: '',
            emailRules: [
                v => !!v || '  .',
                v => /.+@.+\..+/.test(v) || '   .',
            ],
            password: '',
            passwordRules: [
                v => !!v || '  .',
                v => (v && v.length >= 6) || ' 6 .',
            ],

            usersRef: null
        }),
        methods: {
            validate () {
                if (this.$refs.form.validate()) {
                    this.doLogin();
                }
            },
            doLogin() {
                const loader = this.$common.getLoader(this);
                this.$firebase.auth()
                    .signInWithEmailAndPassword(this.email, this.password)
                    .then(({user}) => {
                        this.$store.dispatch('setUser', user);
                        this.$snack['success']({
                            text: `${user.displayName} .`
                        });
                        this.$router.replace('/');
                    }).catch(err => {
                        const msg = err.code === "auth/user-not-found" ? "  ." : err.message;
                    this.$alert.showAlertToWarning(" ", msg);
                    loader.hide();
                });
            }
        },
        created() {
            this.usersRef = this.$firebase.database().ref('users');
        }
    };
</script>
